.item{
    position: relative;
    left: 41%;
    top: 29%;
    border: 3px solid #EDF2EF;
    border-radius: 16px;
    width: 55%;
    height: 400px;
    margin: 16px;
    background-color: #EDF2EF;
}

.formulario-arancel{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.boton-y-titulo{
    display: flex;
    width: 100%;
    justify-content: flex-start;
    margin: 5px 0;
}

.atras{
    margin: 0 12rem 0 20px;
    width: 60px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid #EABE3F;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    color: black;
    background-color: #EABE3F;
}

.preguntas-formu{
    display: flex;
    width: 450px;
    align-items: center;
    margin: 10px;
    flex-wrap: wrap;
    justify-content: center;
}

.preguntas-formu label{
    font-size: 1.1rem;
    margin: 5px 10px;
}

.respuesta-formu{
    width: 180px;
    height: 28px;
    border: 2px solid #1E2EDE;
    border-radius: 5px;
    padding-left: 10px;
    margin: 5px 0;
    font-size: 0.9rem;
}

.pointer{
    cursor: pointer;

}

.respuesta-formu:focus{
    border-color: transparent;
    outline-color: #A8201A;
}

.button-guardar{
    margin: 15px 10px;
    width: 110px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid #1E2EDE;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    color: white;
    background-color: #1E2EDE;
}