.fondo-sesion{
    background-image: url("../assets/color-2174065_1280.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    min-height: 100vh;
}

.sesion-activa{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.datos-usuario{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 10px;
    width: 99%;
    height: 40px;
    /* background-color: #1E2EDE; */
    background-color: #EDF2EF;
    border: 2px solid #1E2EDE;
    border-radius: 10px;

}

.datos-usuario > p{
    margin: 0;
    padding: 0;
    font-size: 1.1rem;
    font-weight: bold;
    /* color: white; */
    /* color: #00aae4; */
    color: #1E2EDE;
}

.navbar-info{
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
}

.pagina-principal{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 55%;
    height: 400px;
    background-color: #EDF2EF;
    border-radius: 16px;
}

.pagina-principal > p{
    font-size: 1rem;
    margin: 20px;
    text-align: center;
}
.pagina-principal > p > span{
    font-weight: bold;
}

.cerrar-sesion{
    margin: 5px;
    width: 140px;
    height: 35px;
    border-radius: 10px;
    border: 2px solid transparent;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    background-color: #EABE3F;
}

.cerrar-sesion:hover{
    box-shadow: 0 0 10px black;
}