.navbar{
    display: flex;
    flex-direction: column;
    width: 35%;
    height: 400px;
    background-color: #EDF2EF;
    border-radius: 16px;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: scroll;
}
.navbar::-webkit-scrollbar {
    -webkit-appearance: none;
}
.navbar::-webkit-scrollbar {
    width: 10px;
}
.navbar::-webkit-scrollbar-thumb {
    background-color: #00aae4;
    border-radius: 20px;
}
.navbar::-webkit-scrollbar-button{
    margin: 2px;
}

.button-navbar{
    height: 35px;
    margin: 10px;
    border-radius: 10px;
    border: 2px solid transparent;
    font-family: 'Old Standard TT', serif;
    /* background-color:#B9B8B5; */
    background-color: #00aae4;
    box-shadow: 0 0 15px #2271b3;
    opacity: 0.9;
    color: white;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
}

.inscripcion{
    margin: 8px 0;
    padding: 0 30px;
}

.inscripcion > li{
    list-style: none;
}

.inscripcion p{
    font-size: 1rem;
    margin: 8px 0 0 0;
}

.inscripcion > li > button{
    font-size: 1rem;
    background-color: transparent;
    border: 1px solid transparent;
    color: black;
    font-family: 'Old Standard TT', serif;
    cursor: pointer;
}

.inscripcion button:hover{
    text-shadow: 0px 0px 10px black;
}

.button-navbar:hover{
    box-shadow: 0 0 15px black;
}
