.contenedor-recibo{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 20px 0;
    height: 350px;
}
.recibo{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;
}


.label-recibo{
    font-size: 1.1rem;
    margin: 5px 10px;
    font-weight: bold;
}

.label-recibo span{
    font-weight: lighter;
}