.inicio-info{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.text-info{
    font-size: 1.2rem;
    margin-bottom: 10px;
    font-weight: bold;
    text-align: center;
}

.button-info{
    color: white;
    width: 150px;
    height: 35px;
    border-radius: 10px;
    border: 2px solid #FE7F2D;
    background-color: #FE7F2D;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    transform: scale(1);
}

.button-info:hover{
    transform: scale(1.1);
    background-color: #FE7F2D;
    box-shadow: 0 0 10px #FE7F2D, 0 0 20px #FE7F2D, 0 0 40px #FE7F2D;
}