.fondo{
    background-image: url("../assets/ecsa.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    min-height: 100vh;
}

.form{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.formulario{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
    height: min-content;
    margin-top: 20px;
    padding: 10px;
    background: transparent;
    backdrop-filter: blur(30px);
    border: 1px solid gray;
    border-radius: 16px;
    color: white;
}

.subtitulo{
    font-size: 1.5rem;
    margin: 10px 0;
    color: #0D0106;
}

.preguntas{
    display: flex;
    /* flex-direction: column; */
    width: 400px;
    align-items: flex-start;
    margin: 10px;
    flex-wrap: wrap;
    justify-content: center;
}

.descripcion{
    font-size: 1.1rem;
    font-weight: bold;
    margin: 8px 0;
}

.respuesta{
    width: 120px;
    height: 24px;
    border: 2px solid #A8201A;
    border-radius: 5px;
    padding-left: 10px;
    margin-left: 10px;
}

.respuesta:focus{
    border-color: transparent;
    outline-color: #1E2EDE;
}

.button-crear-clave, .button-restaurar-clave, .regresar-panta{
    margin: 15px 10px;
    width: 110px;
    height: 35px;
    border-radius: 10px;
    border: 2px solid transparent;
    background-color: #E6E8E6;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
}

.nota, .alert{
    font-size: 16px;
    text-align: center;
    margin: 8px 0;
}


.alert{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #E6E8E6;
    font-weight: bold;
    background-color: #A8201A;
    border: 1px solid #E6E8E6;
    border-radius: 10px;
    height: 30px;
    width: 300px;
}

.botones{
    display: flex;
    justify-content: center;
    margin: 10px;
}

.button-crear-clave, .button-restaurar-clave{
    width: 210px;
    color: white;
}

.botones > button:hover, .regresar-panta:hover{
    box-shadow: 0 0 10px white;
}

.button-crear-clave{
    border-color: #1E2EDE;
    background-color: #1E2EDE;
}
.button-restaurar-clave{
    border-color: #8AC926;
    background-color: #8AC926;
}

.regresar-panta{
    border-color: #EABE3F;
    background-color: #EABE3F;
}