.fondo-info{
    background-image: url('../assets/cursosBasicos.webp');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    min-height: 100vh;
}

.info-dace{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.informacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    height: min-content;
    padding: 10px;
    background: transparent;
    backdrop-filter: blur(10px);
    border: 2px solid gray;
    border-radius: 16px;
    color: white;
    margin-bottom: 10px;
}

.subtitulo{
    font-size: 1.5rem;
    margin: 10px 0;
    color: #0D0106;
}

.info-semestre{
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
    margin: 8px;
    padding: 10px 0;
    color: #0D0106;
    border: 3px solid #A8201A;
    border-left: transparent;
    border-right: transparent;
}

.nota-arancel{
    font-size: 16px;
    text-align: center;
}
.nota-arancel span{
    font-weight: bold;
}

.buttons-info{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

button a{
    text-decoration: none;
    color: black;
}

.buttons-info > button, .regresar{
    margin: 5px;
    width: 240px;
    height: 35px;
    border-radius: 10px;
    border: 2px solid transparent;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
}

.buttons-info > button:hover, .regresar:hover{
    box-shadow: 0 0 15px white;
}

.button-dace{
    border-color: #1E2EDE;
    background-color: #1E2EDE;
}
.button-egresado{
    border-color: #A8201A;
    background-color: #A8201A;
}
.button-arancel{
    border-color: #FE7F2D;
    background-color: #FE7F2D;
}
.button-censo{
    border-color: #E6E8E6;
    background-color: #E6E8E6;
}
.button-resolucion{
    border-color: #8AC926;
    background-color: #8AC926;
}

.regresar{
    width: 100px;
    border-color: #EABE3F;
    background-color: #EABE3F;
}