.button-ingresar-sesion{
    margin: 15px 10px;
    width: 110px;
    height: 35px;
    border-radius: 10px;
    color: black;
    border: 2px solid transparent;
    background-color: #E6E8E6;
    font-family: 'Old Standard TT', serif;
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
}

.button-ingresar-sesion:hover{
    box-shadow: 0 0 10px white;
}